<template>
  <div class="vtl vtl-card p-2">
    <div class="vtl-card-title" v-if="title">{{ title }}</div>
    <div class="vtl-card-body">
      <div class="vtl-row row">
        <div class="col-12 dataTables_wrapper" :class="{
          'fixed-first-column': isFixedFirstColumn,
          'fixed-first-second-column': isFixedFirstColumn && hasCheckbox,
        }">
          <div v-if="isLoading" class="vtl-loading-mask">
            <div class="vtl-loading-content">
              <span style="color: white">Loading...</span>
            </div>
          </div>
          <table class="
              vtl-table
              vtl-table-hover
              vtl-table-bordered
              table table-bordered table-hover
              align-items-center align-middle
              text-center
              justify-content-center
            " ref="localTable">
            <thead class="vtl-thead sticky-top" style="z-index: 100;">
              <tr class="vtl-thead-tr m-0 p-0">
                <th v-if="hasCheckbox" class="vtl-thead-th vtl-checkbox-th m-0 p-0 align-items-center align-middle">
                  <div>
                    <input type="checkbox" class="vtl-thead-checkbox" v-model="setting.isCheckAll" />
                  </div>
                </th>
                <th v-for="(col, index) in columns" class="vtl-thead-th m-0 p-0" :class="col.headerClasses" :key="index"
                  :style="
                    Object.assign(
                      {
                        width: col.width ? col.width : 'auto',
                      },
                      col.headerStyles
                    )
                  ">
                  <div class="vtl-thead-column" :class="{
                    'vtl-sortable': col.sortable,
                    'vtl-both': col.sortable,
                    'vtl-asc':
                      setting.order === col.field && setting.sort === 'asc',
                    'vtl-desc':
                      setting.order === col.field && setting.sort === 'desc',
                  }" @click="col.sortable ? doSort(col.field) : false">
                    {{ col.label }}
                  </div>
                </th>
              </tr>
            </thead>
            <tbody v-if="rows.length > 0" class="vtl-tbody">
              <template v-if="isStaticMode">
                <tr v-for="(row, i) in localRows" :key="i" class="vtl-tbody-tr" :class="
                  typeof rowClasses === 'function'
                    ? rowClasses(row)
                    : rowClasses
                " @click="$emit('row-clicked', row)">
                  <td v-if="hasCheckbox" class="vtl-tbody-td m-0 p-0">
                    <div>
                      <!-- <input
                        type="checkbox"
                        class="vtl-tbody-checkbox"
                        :ref="
                          (el) => {
                            rowCheckbox[i] = el;
                          }
                        "
                        :value="row[setting.keyColumn]"
                        @click="checked"
                      /> -->
                      <input type="checkbox" class="vtl-tbody-checkbox" :ref="
                        (el) => {
                          rowCheckbox[i] = el;
                        }
                      " :value="i" :id="'checkbox' + i" @click="checked" />
                    </div>
                  </td>
                  <td v-for="(col, j) in columns" :key="j" class="vtl-tbody-td py-0 m-0" align="center"
                    :class="col.columnClasses" :style="col.columnStyles">
                    <div v-if="col.display" v-html="col.display(row, i)"></div>
                    <template v-else>
                      <div v-if="setting.isSlotMode && slots[col.field]">
                        <slot :name="col.field" :row="row" :key="i"></slot>
                      </div>
                      <span v-else>{{ row[col.field] }}</span>
                    </template>
                  </td>
                </tr>
              </template>
              <template v-else>
                <tr v-for="(row, i) in rows" :key="i" class="vtl-tbody-tr" :class="
                  typeof rowClasses === 'function'
                    ? rowClasses(row)
                    : rowClasses
                " @click="$emit('row-clicked', row)">
                  <td v-if="hasCheckbox" class="vtl-tbody-td p-0 m-0">
                    <div>
                      <input type="checkbox" class="vtl-tbody-checkbox" :ref="
                        (el) => {
                          rowCheckbox[i] = el;
                        }
                      " :value="row[setting.keyColumn]" @click="checked" />
                    </div>
                  </td>
                  <td v-for="(col, j) in columns" :key="j" class="vtl-tbody-td py-0 m-0" align="center"
                    :class="col.columnClasses" :style="col.columnStyles">
                    <div v-if="col.display" v-html="col.display(row, i)"></div>
                    <div v-else>
                      <div v-if="setting.isSlotMode && slots[col.field]">
                        <slot :name="col.field" :row="row" :key="i"></slot>
                      </div>
                      <span v-else>{{ row[col.field] }}</span>
                    </div>
                  </td>
                </tr>
              </template>
            </tbody>
          </table>
        </div>
      </div>
      <div class="vtl-paging vtl-row" v-if="rows.length > 0">
        <template v-if="!setting.isHidePaging">
          <div class="vtl-paging-info col col-auto p-0 m-0">
            <div role="status" aria-live="polite">
              {{
                  stringFormat(
                    messages.pagingInfo,
                    setting.offset,
                    setting.limit,
                    total
                  )
              }}
            </div>
          </div>
          <div class="w-100" style="height: 1ch"></div>
          <!-- </div>breaks cols to a new line-->
          <div class="row w-100 justify-content-between align-items-center p-0 m-0">
            <div class="vtl-paging-change-div col col-auto p-0 m-0">
              <span class="vtl-paging-count-label col col-auto pe-0">{{
                  messages.pageSizeChangeLabel
              }}</span>
              <select class="vtl-paging-count-dropdown" v-model="setting.pageSize">
                <option v-for="pageOption in pageOptions" :value="pageOption.value" :key="pageOption.value">
                  {{ pageOption.text }}
                </option>
              </select>
              <span class="vtl-paging-page-label col col-auto pe-0">{{
                  messages.gotoPageLabel
              }}</span>
              <select class="vtl-paging-page-dropdown col col-auto" v-model="setting.page">
                <option v-for="n in setting.maxPage" :key="n" :value="parseInt(n)">
                  {{ n }}
                </option>
              </select>
            </div>

            <div class="
                vtl-paging-pagination-div
                align-items-center
                col col-auto
                p-0
                m-0
              ">
              <div class="dataTables_paginate">
                <ul class="vtl-paging-pagination-ul vtl-pagination p-0 m-0">
                  <li class="
                      vtl-paging-pagination-page-li
                      vtl-paging-pagination-page-li-first
                      page-item
                    " :class="{ disabled: setting.page <= 1 }">
                    <a class="
                        vtl-paging-pagination-page-link
                        vtl-paging-pagination-page-link-first
                        page-link
                      " href="javascript:void(0)" aria-label="Previous" @click="setting.page = 1">
                      <span aria-hidden="true">
                        <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor"
                          class="bi bi-skip-start-fill" viewBox="0 0 16 16">
                          <path
                            d="M4 4a.5.5 0 0 1 1 0v3.248l6.267-3.636c.54-.313 1.232.066 1.232.696v7.384c0 .63-.692 1.01-1.232.697L5 8.753V12a.5.5 0 0 1-1 0V4z" />
                        </svg>
                      </span>
                      <span class="sr-only">First</span>
                    </a>
                  </li>
                  <li class="
                      vtl-paging-pagination-page-li
                      vtl-paging-pagination-page-li-prev
                      page-item
                    " :class="{ disabled: setting.page <= 1 }">
                    <a class="
                        vtl-paging-pagination-page-link
                        vtl-paging-pagination-page-link-prev
                        page-link
                      " href="javascript:void(0)" aria-label="Previous" @click="prevPage">
                      <span aria-hidden="true">
                        <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor"
                          class="bi bi-caret-left-fill" viewBox="0 0 16 16">
                          <path
                            d="m3.86 8.753 5.482 4.796c.646.566 1.658.106 1.658-.753V3.204a1 1 0 0 0-1.659-.753l-5.48 4.796a1 1 0 0 0 0 1.506z" />
                        </svg>
                      </span>
                      <span class="sr-only">Prev</span>
                    </a>
                  </li>
                  <li class="
                      vtl-paging-pagination-page-li
                      vtl-paging-pagination-page-li-number
                      page-item
                    " v-for="n in setting.paging" :key="n" :class="{ disabled: setting.page === n }">
                    <a class="
                        vtl-paging-pagination-page-link
                        vtl-paging-pagination-page-link-number
                        page-link
                      " href="javascript:void(0)" @click="movePage(n)">{{ n }}</a>
                  </li>
                  <li class="
                      vtl-paging-pagination-page-li
                      vtl-paging-pagination-page-li-next
                      page-item
                    " :class="{ disabled: setting.page >= setting.maxPage }">
                    <a class="
                        vtl-paging-pagination-page-link
                        vtl-paging-pagination-page-link-next
                        page-link
                      " href="javascript:void(0)" aria-label="Next" @click="nextPage">
                      <span aria-hidden="true"><svg xmlns="http://www.w3.org/2000/svg" width="16" height="16"
                          fill="currentColor" class="bi bi-caret-right-fill" viewBox="0 0 16 16">
                          <path
                            d="m12.14 8.753-5.482 4.796c-.646.566-1.658.106-1.658-.753V3.204a1 1 0 0 1 1.659-.753l5.48 4.796a1 1 0 0 1 0 1.506z" />
                        </svg>
                      </span>
                      <span class="sr-only">Next</span>
                    </a>
                  </li>
                  <li class="
                      vtl-paging-pagination-page-li
                      vtl-paging-pagination-page-li-last
                      page-item
                    " :class="{ disabled: setting.page >= setting.maxPage }">
                    <a class="
                        vtl-paging-pagination-page-link
                        vtl-paging-pagination-page-link-last
                        page-link
                      " href="javascript:void(0)" aria-label="Next" @click="setting.page = setting.maxPage">
                      <span aria-hidden="true">
                        <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor"
                          class="bi bi-skip-end-fill" viewBox="0 0 16 16">
                          <path
                            d="M12.5 4a.5.5 0 0 0-1 0v3.248L5.233 3.612C4.693 3.3 4 3.678 4 4.308v7.384c0 .63.692 1.01 1.233.697L11.5 8.753V12a.5.5 0 0 0 1 0V4z" />
                        </svg>
                      </span>
                      <span class="sr-only">Last</span>
                    </a>
                  </li>
                </ul>
              </div>
            </div>
          </div>
        </template>
      </div>
      <div class="vtl-row" v-else>
        <div class="vtl-empty-msg col-12 text-center">
          {{ messages.noDataAvailable }}
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import {
  defineComponent,
  ref,
  reactive,
  computed,
  watch,
  onBeforeUpdate,
  nextTick,
  onMounted,
} from "vue";

export default defineComponent({
  name: "my-table",
  emits: [
    "return-checked-rows",
    "do-search",
    "is-finished",
    "get-now-page",
    "row-clicked",
  ],
  props: {
    //  (is data loading)
    isLoading: {
      type: Boolean,
      require: true,
    },
    //  (Whether to perform a re-query)
    isReSearch: {
      type: Boolean,
      require: true,
    },
    // Checkbox (Presence of Checkbox)
    hasCheckbox: {
      type: Boolean,
      default: false,
    },
    // Checkbox (Returns data type for checked of Checkbox)
    checkedReturnType: {
      type: String,
      default: "key",
    },
    //  (title)
    title: {
      type: String,
      default: "",
    },
    //  (Fixed first column's position)
    isFixedFirstColumn: {
      type: Boolean,
      default: false,
    },
    //  (Field)
    columns: {
      type: Array,
      default: () => {
        return [];
      },
    },
    //  (data)
    rows: {
      type: Array,
      default: () => {
        return [];
      },
    },
    //  (data row classes)
    rowClasses: {
      type: [Array, Function],
      default: () => {
        return [];
      },
    },
    //  (Display the number of items on one page)
    pageSize: {
      type: Number,
      default: 10,
    },
    //  (Total number of transactions)
    total: {
      type: Number,
      default: 100,
    },
    //  (Current page number)
    page: {
      type: Number,
      default: 1,
    },
    //  (Sort condition)
    sortable: {
      type: Object,
      default: () => {
        return {
          order: "id",
          sort: "asc",
        };
      },
    },
    //  (Display text)
    messages: {
      type: Object,
      default: () => {
        return {
          pagingInfo: "Showing {0}-{1} of {2}",
          pageSizeChangeLabel: "Row count:",
          gotoPageLabel: "Go to page:",
          noDataAvailable: "No data",
        };
      },
    },
    //  (Static mode(no refresh server data))
    isStaticMode: {
      type: Boolean,
      default: false,
    },
    //  (V-slot mode)
    isSlotMode: {
      type: Boolean,
      default: false,
    },
    //  (Hide paging)
    isHidePaging: {
      type: Boolean,
      default: false,
    },
    //  (Dropdown of Display the number of items on one page)
    pageOptions: {
      type: Array,
      default: () => [
        {
          value: 10,
          text: 10,
        },
        {
          value: 25,
          text: 25,
        },
        {
          value: 50,
          text: 50,
        },
      ],
    },
  },
  setup(props, { emit, slots }) {
    let localTable = ref(null);

    //  (Validate dropdown's values have page-size value or not)
    let defaultPageSize =
      props.pageOptions.length > 0
        ? ref(props.pageOptions[0].value)
        : ref(props.pageSize);
    if (props.pageOptions.length > 0) {
      props.pageOptions.forEach((v) => {
        if (
          Object.prototype.hasOwnProperty.call(v, "value") &&
          Object.prototype.hasOwnProperty.call(v, "text") &&
          props.pageSize == v.value
        ) {
          defaultPageSize.value = v.value;
        }
      });
    }

    //  (Internal set value for components)
    const setting = reactive({
      // Slot (Enable slot mode)
      isSlotMode: props.isSlotMode,
      //  (Whether to select all)
      isCheckAll: false,
      //  (Hide paging)
      isHidePaging: props.isHidePaging,
      // KEY (KEY field name)
      keyColumn: computed(() => {
        let key = "";
        Object.assign(props.columns).forEach((col) => {
          if (col.isKey) {
            key = col.field;
          }
        });
        return key;
      }),
      //  (current page number)
      page: props.page,
      //  (Display count per page)
      pageSize: defaultPageSize.value,
      //  (Maximum number of pages)
      maxPage: computed(() => {
        if (props.total <= 0) {
          return 0;
        }
        let maxPage = Math.floor(props.total / setting.pageSize);
        let mod = props.total % setting.pageSize;
        if (mod > 0) {
          maxPage++;
        }
        return maxPage;
      }),
      //  (The starting value of the page number)
      offset: computed(() => {
        return (setting.page - 1) * setting.pageSize + 1;
      }),
      //  (Maximum number of pages0
      limit: computed(() => {
        let limit = setting.page * setting.pageSize;
        return props.total >= limit ? limit : props.total;
      }),
      //  (Paging array)
      paging: computed(() => {
        let startPage = setting.page - 2 <= 0 ? 1 : setting.page - 2;
        if (setting.maxPage - setting.page <= 2) {
          startPage = setting.maxPage - 4;
        }
        startPage = startPage <= 0 ? 1 : startPage;
        let pages = [];
        for (let i = startPage; i <= setting.maxPage; i++) {
          if (pages.length < 5) {
            pages.push(i);
          }
        }
        return pages;
      }),
      //  (Sortable for local)
      order: props.sortable.order,
      sort: props.sortable.sort,
      pageOptions: props.pageOptions,
    });

    //  (Data rows for local)
    const localRows = computed(() => {
      let rows = props.rows;
      // refs https://developer.mozilla.org/en-US/docs/Web/JavaScript/Reference/Global_Objects/Intl/Collator/compare
      var collator = new Intl.Collator(undefined, {
        numeric: true,
        sensitivity: "base",
      });
      let sortOrder = setting.sort === "desc" ? -1 : 1;
      rows.sort(function (a, b) {
        return collator.compare(a[setting.order], b[setting.order]) * sortOrder;
      });

      // return sorted and offset rows
      let result = [];
      for (let index = setting.offset - 1; index < setting.limit; index++) {
        if (rows[index]) {
          result.push(rows[index]);
        }
      }

      nextTick(function () {
        // 
        callIsFinished();
      });

      return result;
    });

    ////////////////////////////
    //
    //  Checkbox 
    //  (Checkbox related operations)
    //

    // Checkbox (Define Checkbox reference)
    const rowCheckbox = ref([]);
    if (props.hasCheckbox) {
      /**
       *  (Execute before re-rendering)
       */
      onBeforeUpdate(() => {
        //  (Clear all values before each update)
        rowCheckbox.value = [];
      });

      /**
       * Checkbox (Check all checkboxes for monitoring)
       */
      watch(
        () => setting.isCheckAll,
        (state) => {
          let isChecked = [];
          rowCheckbox.value.forEach((val, i) => {
            if (val) {
              val.checked = state;
              if (val.checked) {
                if (props.checkedReturnType == "row") {
                  isChecked.push(localRows.value[i]);
                } else {
                  isChecked.push(val.value);
                }
              }
            }
          });
          //  (Return the selected data on the screen)
          emit("return-checked-rows", isChecked);
        }
      );
    }

    /**
     * Checkbox (Checkbox click event)
     */
    const checked = (event) => {
      event.stopPropagation();
      let isChecked = [];
      rowCheckbox.value.forEach((val, i) => {
        if (val && val.checked) {
          if (props.checkedReturnType == "row") {
            isChecked.push(localRows.value[i]);
          } else {
            isChecked.push(val.value);
          }
        }
      });
      //  (Return the selected data on the screen)
      emit("return-checked-rows", isChecked);
    };

    /**
     *  (Clear all selected data on the screen)
     */
    const clearChecked = () => {
      rowCheckbox.value.forEach((val) => {
        if (val && val.checked) {
          val.checked = false;
        }
      });
      //  (Return the selected data on the screen)
      emit("return-checked-rows", []);
    };

    ////////////////////////////
    //
    //   
    //  (Sorting, page change, etc. related operations)
    //

    /**
     *  (Call execution sequencing)
     */
    const doSort = (order) => {
      let sort = "asc";
      if (order == setting.order) {
        //  (When sorting items)
        if (setting.sort == "asc") {
          sort = "desc";
        }
      }
      let offset = (setting.page - 1) * setting.pageSize;
      let limit = setting.pageSize;
      setting.order = order;
      setting.sort = sort;
      emit("do-search", offset, limit, order, sort);

      //  (Clear the selected data on the screen)
      if (setting.isCheckAll) {
        //  (It will be cleared when you cancel all selections)
        setting.isCheckAll = false;
      } else {
        if (props.hasCheckbox) {
          clearChecked();
        }
      }
    };

    /**
     *  (Switch page number)
     *
     * @param page      number      (New page number)
     * @param prevPage  number    (Current page number)
     */
    const changePage = (page, prevPage) => {
      setting.isCheckAll = false;
      let order = setting.order;
      let sort = setting.sort;
      let offset = (page - 1) * setting.pageSize;
      let limit = setting.pageSize;
      if (!props.isReSearch || page > 1 || page == prevPage) {
        //  (Call query will only be executed if the page number is changed without re-query)
        emit("do-search", offset, limit, order, sort);
      }
    };
    //  (Monitor page switching)
    watch(() => setting.page, changePage);
    //  (Monitor manual page switching)
    watch(
      () => props.page,
      (val) => {
        if (val <= 1) {
          setting.page = 1;
          emit("get-now-page", setting.page);
        } else if (val >= setting.maxPage) {
          setting.page = setting.maxPage;
          emit("get-now-page", setting.page);
        } else {
          setting.page = val;
        }
      }
    );

    /**
     *  (Switch display number)
     */
    const changePageSize = () => {
      if (setting.page === 1) {
        //  changePage()
        changePage(setting.page, setting.page);
      } else {
        // , changePage()
        setting.page = 1;
        setting.isCheckAll = false;
      }
    };
    //  (Monitor display number switch from component)
    watch(() => setting.pageSize, changePageSize);
    // Prop (Monitor display number switch from prop)
    watch(
      () => props.pageSize,
      (newPageSize) => {
        setting.pageSize = newPageSize;
      }
    );

    /**
     *  (Previous page)
     */
    const prevPage = () => {
      if (setting.page == 1) {
        //  (If it is the first page, it will not be executed)
        return false;
      }
      setting.page--;
    };

    /**
     *  (Move to the specified number of pages)
     */
    const movePage = (page) => {
      setting.page = page;
    };

    /**
     *  (Next page)
     */
    const nextPage = () => {
      if (setting.page >= setting.maxPage) {
        //  (If it is equal to or greater than the maximum number of pages, no execution)
        return false;
      }
      setting.page++;
    };

    //  (Monitoring data changes)
    watch(
      () => props.rows,
      () => {
        if (props.isReSearch || props.isStaticMode) {
          setting.page = 1;
        }
        nextTick(function () {
          //  (Return the private components after the data is rendered)
          if (!props.isStaticMode) {
            callIsFinished();
          }
        });
      }
    );

    const stringFormat = (template, ...args) => {
      return template.replace(/{(\d+)}/g, function (match, number) {
        return typeof args[number] != "undefined" ? args[number] : match;
      });
    };

    // Call is-finished Method
    const callIsFinished = () => {
      if (localTable.value) {
        let localElement =
          localTable.value.getElementsByClassName("is-rows-el");
        emit("is-finished", localElement);
      }
      emit("get-now-page", setting.page);
    };

    /**
     *  (Mounted Event)
     */
    onMounted(() => {
      nextTick(() => {
        if (props.rows.length > 0) {
          callIsFinished();
        }
      });
    });

    if (props.hasCheckbox) {
      //  Checkbox  (When Checkbox is needed)
      return {
        slots,
        localTable,
        localRows,
        setting,
        rowCheckbox,
        checked,
        doSort,
        prevPage,
        movePage,
        nextPage,
        stringFormat,
      };
    } else {
      return {
        slots,
        localTable,
        localRows,
        setting,
        doSort,
        prevPage,
        movePage,
        nextPage,
        stringFormat,
      };
    }
  },
});
</script>

<style scoped>
.vtl-thead-column {
  border: 1px solid darkgray !important;
}

.vtl-checkbox-th {
  width: 3.5ch;
