<template>
    <div class="row" style="text-align: left">
        <div class="col col-auto">
            <label for="pnInput" class="col-form-label"
                >{{ $t("basicInfoLang.quicksearch") }} :</label
            >
        </div>
        <div class="col col-3 p-0 m-0">
            <input
                id="pnInput"
                class="text-center form-control form-control-lg"
                v-bind:placeholder="$t('basicInfoLang.enterisn')"
                v-model="searchTerm"
            />
        </div>
    </div>
    <div class="w-100" style="height: 1ch"></div>
    <!-- </div>breaks cols to a new line-->
    <table-lite
        :is-fixed-first-column="true"
        :isStaticMode="true"
        :isSlotMode="true"
        :hasCheckbox="true"
        :messages="table.messages"
        :columns="table.columns"
        :rows="table.rows"
        :total="table.totalRecordCount"
        :page-options="table.pageOptions"
        :sortable="table.sortable"
        @do-search="doSearch"
        @is-finished="table.isLoading = false"
        @return-checked-rows="updateCheckedRows"
    >
        <template v-slot:="{ row, key }">
            <div v-if="row. == ''">
                <select
                    @change="(event) => (row. = event.target.value)"
                    style="width: 7ch"
                    class="col col-auto form-select form-select-lg p-0 m-0"
                    :id="'month' + key"
                    :name="'month' + key"
                >
                    <option value="" selected>
                        {{ $t("basicInfoLang.yes") }}
                    </option>
                    <option value="">{{ $t("basicInfoLang.no") }}</option>
                </select>
            </div>
            <div v-else>
                <select
                    @change="(event) => (row. = event.target.value)"
                    style="width: 7ch"
                    class="col col-auto form-select form-select-lg p-0 m-0"
                    :id="'month' + key"
                    :name="'month' + key"
                >
                    <option value="">{{ $t("basicInfoLang.yes") }}</option>
                    <option value="" selected>
                        {{ $t("basicInfoLang.no") }}
                    </option>
                </select>
            </div>
        </template>

        <template v-slot:="{ row, key }">
            <div v-if="row. == ''">
                <input
                    class="form-control text-center p-0 m-0"
                    style="width: 8ch"
                    type="number"
                    :id="'safe' + key"
                    :name="'safe' + key"
                    :value="row."
                    min="0"
                />
            </div>
            <div v-else>{{ $t("basicInfoLang.differ_by_client") }}</div>
        </template>
    </table-lite>
</template>

<script>
import { defineComponent, reactive, ref, computed } from "vue";
import {
    getCurrentInstance,
    onBeforeMount,
    onMounted,
    watch,
} from "@vue/runtime-core";
import TableLite from "./TableLite.vue";
import useConsumptiveMaterials from "../../composables/ConsumptiveMaterials.ts";
export default defineComponent({
    name: "App",
    components: { TableLite },
    setup() {
        const { mats, getMats } = useConsumptiveMaterials(); // axios get the mats data

        onBeforeMount(getMats);

        const searchTerm = ref(""); // Search text
        const app = getCurrentInstance(); // get the current instance
        let thisHtmlLang = document
            .getElementsByTagName("HTML")[0]
            .getAttribute("lang");
        // get the current locale from html tag
        app.appContext.config.globalProperties.$lang.setLocale(thisHtmlLang); // set the current locale to vue package

        // pour the data in
        const data = reactive([]);
        const senders = reactive([]); // access the value by senders[0], senders[1] ...

        watch(mats, () => {
            console.log(JSON.parse(mats.value)); // test
            let allRowsObj = JSON.parse(mats.value);
            console.log(allRowsObj.datas.length);
            for (let i = 0; i < allRowsObj.senders.length; i++) {
                senders.push(allRowsObj.senders[i]);
            } // for

            for (let i = 0; i < allRowsObj.datas.length; i++) {
                data.push(allRowsObj.datas[i]);
            } // for

            document.getElementById("QueryFlag").click();
        }); // watch for data change

        // Table config
        const table = reactive({
            isLoading: false,
            columns: [
                {
                    label: app.appContext.config.globalProperties.$t(
                        "basicInfoLang.isn"
                    ),
                    field: "",
                    width: "14ch",
                    sortable: true,
                    isKey: true,
                    display: function (row, i) {
                        // console.log(row);
                        return (
                            '<input type="hidden" id="number' +
                            i +
                            '" name="number' +
                            i +
                            '" value="' +
                            row. +
                            '">' +
                            '<div class="text-nowrap scrollableWithoutScrollbar"' +
                            ' style="overflow-x: auto !important; width: 100%; -ms-overflow-style: none !important; scrollbar-width: none !important;">' +
                            row. +
                            "</div>"
                        );
                    },
                },
                {
                    label: app.appContext.config.globalProperties.$t(
                        "basicInfoLang.pName"
                    ),
                    field: "",
                    width: "13ch",
                    sortable: true,
                    display: function (row, i) {
                        return (
                            '<input type="hidden" id="name' +
                            i +
                            '" name="name' +
                            i +
                            '" value="' +
                            row. +
                            '">' +
                            '<div class="text-nowrap scrollableWithoutScrollbar"' +
                            ' style="overflow-x: auto; width: 100%;">' +
                            row. +
                            "</div>"
                        );
                    },
                },
                {
                    label: app.appContext.config.globalProperties.$t(
                        "basicInfoLang.format"
                    ),
                    field: "",
                    width: "13ch",
                    sortable: true,
                    display: function (row, i) {
                        return (
                            '<input type="hidden" id="format' +
                            i +
                            '" name="format' +
                            i +
                            '" value="' +
                            row. +
                            '">' +
                            '<div class="scrollableWithoutScrollbar text-nowrap"' +
                            ' style="overflow-x: auto; width: 100%;">' +
                            row. +
                            "</div>"
                        );
                    },
                },
                {
                    label: app.appContext.config.globalProperties.$t(
                        "basicInfoLang.price"
                    ),
                    field: "",
                    width: "13ch",
                    sortable: true,
                    display: function (row, i) {
                        return (
                            '<input style="width: 10ch;" type="number" step="0.00001" id="price' +
                            i +
                            '"' +
                            ' class="form-control text-center p-0 m-0" name="price' +
                            i +
                            '"' +
                            ' value="' +
                            parseFloat(row.) +
                            '">'
                        );
                    },
                },
                {
                    label: app.appContext.config.globalProperties.$t(
                        "basicInfoLang.money"
                    ),
                    field: "",
                    width: "10ch",
                    sortable: true,
                    display: function (row, i) {
                        let currencyDict = [
                            "RMB",
                            "USD",
                            "JPY",
                            "TWD",
                            "VND",
                            "IDR",
                        ];
                        let returnStr = "";
                        returnStr +=
                            '<select style="width: 8ch;" class="form-select form-select-lg p-0 m-0" id="money' +
                            i +
                            '" name="money' +
                            i +
                            '">';
                        currencyDict.forEach((element) => {
                            if (row. === element) {
                                returnStr +=
                                    "<option selected>" + element + "</option>";
                            } // if
                            else {
                                returnStr += "<option>" + element + "</option>";
                            } // else
                        }); // for each in sender array
                        returnStr += "</select>";
                        return returnStr;
                    },
                },
                {
                    label: app.appContext.config.globalProperties.$t(
                        "basicInfoLang.unit"
                    ),
                    field: "",
                    width: "8ch",
                    sortable: true,
                    display: function (row, i) {
                        return (
                            '<input style="width:5ch;" type="text" id="unit' +
                            i +
                            '"' +
                            ' name="unit' +
                            i +
                            '" value="' +
                            row. +
                            '"' +
                            ' class="form-control text-center p-0 m-0">'
                        );
                    },
                },
                {
                    label: app.appContext.config.globalProperties.$t(
                        "basicInfoLang.mpq"
                    ),
                    field: "MPQ",
                    width: "8ch",
                    sortable: true,
                    display: function (row, i) {
                        return (
                            '<input style="width:8ch;" type="number" id="mpq' +
                            i +
                            '"' +
                            ' name="mpq' +
                            i +
                            '" value="' +
                            row.MPQ +
                            '"' +
                            ' class="form-control text-center p-0 m-0" min="0">'
                        );
                    },
                },
                {
                    label: app.appContext.config.globalProperties.$t(
                        "basicInfoLang.moq"
                    ),
                    field: "MOQ",
                    width: "8ch",
                    sortable: true,
                    display: function (row, i) {
                        return (
                            '<input style="width:8ch;" type="number" id="moq' +
                            i +
                            '"' +
                            ' name="moq' +
                            i +
                            '" value="' +
                            row.MOQ +
                            '"' +
                            ' class="form-control text-center p-0 m-0" min="0">'
                        );
                    },
                },
                {
                    label: app.appContext.config.globalProperties.$t(
                        "basicInfoLang.lt"
                    ),
                    field: "LT",
                    width: "8ch",
                    sortable: true,
                    display: function (row, i) {
                        return (
                            '<input style="width:8ch;" type="number" id="lt' +
                            i +
                            '"' +
                            ' name="lt' +
                            i +
                            '" value="' +
                            Math.round(row.LT) +
                            '"' +
                            ' class="form-control text-center p-0 m-0" min="0">'
                        );
                    },
                },
                {
                    label: app.appContext.config.globalProperties.$t(
                        "basicInfoLang.month"
                    ),
                    field: "",
                    width: "12ch",
                    sortable: true,
                },
                {
                    label: app.appContext.config.globalProperties.$t(
                        "basicInfoLang.gradea"
                    ),
                    field: "A",
                    width: "9ch",
                    sortable: true,
                    display: function (row, i) {
                        let returnStr = "";
                        // console.log(row); // test
                        if (row.A === "") {
                            returnStr =
                                '<select style="width: 7ch;" class="col col-auto form-select form-select-lg p-0 m-0"' +
                                ' id="gradea' +
                                i +
                                '" name="gradea' +
                                i +
                                '">' +
                                '<option value="" selected>' +
                                app.appContext.config.globalProperties.$t(
                                    "basicInfoLang.yes"
                                ) +
                                "</option>" +
                                '<option value="">' +
                                app.appContext.config.globalProperties.$t(
                                    "basicInfoLang.no"
                                ) +
                                "</option>" +
                                "</select>";
                        } // if
                        else {
                            returnStr =
                                '<select style="width: 7ch;" class="col col-auto form-select form-select-lg p-0 m-0"' +
                                ' id="gradea' +
                                i +
                                '" name="gradea' +
                                i +
                                '">' +
                                '<option value="">' +
                                app.appContext.config.globalProperties.$t(
                                    "basicInfoLang.yes"
                                ) +
                                "</option>" +
                                '<option value="" selected>' +
                                app.appContext.config.globalProperties.$t(
                                    "basicInfoLang.no"
                                ) +
                                "</option>" +
                                "</select>";
                        } // else

                        return returnStr;
                    },
                },
                {
                    label: app.appContext.config.globalProperties.$t(
                        "basicInfoLang.belong"
                    ),
                    field: "",
                    width: "10ch",
                    sortable: true,
                    display: function (row, i) {
                        let returnStr = "";
                        // console.log(row); // test
                        if (row. === "") {
                            returnStr =
                                '<select style="width: 10ch;" class="col col-auto form-select form-select-lg p-0 m-0"' +
                                ' id="belong' +
                                i +
                                '" name="belong' +
                                i +
                                '">' +
                                '<option value="" selected>' +
                                app.appContext.config.globalProperties.$t(
                                    "basicInfoLang.consume"
                                ) +
                                "</option>" +
                                '<option value="">' +
                                app.appContext.config.globalProperties.$t(
                                    "basicInfoLang.stand"
                                ) +
                                "</option>" +
                                "</select>";
                        } // if
                        else {
                            returnStr =
                                '<select style="width: 10ch;" class="col col-auto form-select form-select-lg p-0 m-0"' +
                                ' id="belong' +
                                i +
                                '" name="belong' +
                                i +
                                '">' +
                                '<option value="">' +
                                app.appContext.config.globalProperties.$t(
                                    "basicInfoLang.consume"
                                ) +
                                "</option>" +
                                '<option value="" selected>' +
                                app.appContext.config.globalProperties.$t(
                                    "basicInfoLang.stand"
                                ) +
                                "</option>" +
                                "</select>";
                        } // else

                        return returnStr;
                    },
                },
                {
                    label: app.appContext.config.globalProperties.$t(
                        "basicInfoLang.senddep"
                    ),
                    field: "",
                    width: "10ch",
                    sortable: true,
                    display: function (row, i) {
                        let returnStr = "";
                        returnStr +=
                            '<select style="width: 10ch;" class="form-select form-select-lg p-0 m-0" id="send' +
                            i +
                            '" name="send' +
                            i +
                            '">';
                        senders.forEach((element) => {
                            if (row. === element) {
                                returnStr +=
                                    "<option selected>" + element + "</option>";
                            } // if
                            else {
                                returnStr += "<option>" + element + "</option>";
                            } // else
                        }); // for each in sender array
                        returnStr += "</select>";
                        return returnStr; // return
                    },
                },
                {
                    label: app.appContext.config.globalProperties.$t(
                        "basicInfoLang.safe"
                    ),
                    field: "",
                    width: "13ch",
                    sortable: true,
                },
            ],
            rows: computed(() => {
                return data.filter((x) =>
                    x.
                        .toLowerCase()
                        .includes(searchTerm.value.toLowerCase())
                );
            }),
            totalRecordCount: computed(() => {
                return table.rows.length;
            }),
            sortable: {
                order: "id",
                sort: "asc",
            },
            messages: {
                pagingInfo:
                    app.appContext.config.globalProperties.$t(
                        "basicInfoLang.now_showing"
                    ) +
                    " {0} ~ {1} " +
                    app.appContext.config.globalProperties.$t(
                        "basicInfoLang.record"
                    ) +
                    ", " +
                    app.appContext.config.globalProperties.$t(
                        "basicInfoLang.total"
                    ) +
                    " {2} " +
                    app.appContext.config.globalProperties.$t(
                        "basicInfoLang.record"
                    ),
                pageSizeChangeLabel: app.appContext.config.globalProperties.$t(
                    "basicInfoLang.records_per_page"
                ),
                gotoPageLabel: app.appContext.config.globalProperties.$t(
                    "basicInfoLang.go_to_page"
                ),
                noDateAvailable: app.appContext.config.globalProperties.$t(
                    "basicInfoLang.search_with_no_data_returned"
                ),
            },
            pageOptions: [
                {
                    value: 10,
                    text: 10,
                },
                {
                    value: 20,
                    text: 20,
                },
                {
                    value: 40,
                    text: 40,
                },
                {
                    value: 60,
                    text: 60,
                },
            ],
        });

        const updateCheckedRows = (rowsKey) => {
            console.log(rowsKey);
        };

        return {
            searchTerm,
            table,
            updateCheckedRows,
        };
    }, // setup
});
</script>
